*{
  margin: 0;
  padding: 0;
}

.logo {
  font-size: 30px;
  font-weight: 500;
  padding: 10px;
}

.box {

  margin: 30px;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 25px;
  font-weight: bold;
  border: 5px solid green;
  border-radius: 100px;
  padding: 10px;
}
.box h4 {
  font-size: 40px;
}
.box p {
  padding: 0;
  margin: 0;

}